<template>
    <div class="qidongye">
      <div class="bj">
        <img src="/img/qdy.png" alt="">
      </div>
      <div class="version">V1.1.1</div>
      <div class="daojishi">
        {{time}}S
      </div>
    </div>
</template>

<script>
export default {
  name: 'qidongye',
  data(){
    return{
      time: 3
    }
  },
  methods:{
    countDown(){
      this.time--
    }
  },
  mounted(){
    this.time=3;
    setInterval(this.countDown,1000);
  },
  watch:{
    time:function(val){
      if(val == 0){
        this.$router.push("/ydy")
      }
    }
  }
}
</script>

<style scoped>
  .daojishi{
    position: absolute;
    top: 30px;
    left: 90%;
    font-size: 26px;
  }
.qidongye{
  width:750px;
  height:1334px;
  position: relative;
}
  .qidongye .bj{
    position: absolute;
    left:0;
    top:0;
  }
  .qidongye .version{
    width:100%;
    position: absolute;
    left: 0;
    bottom: 130px;
    font-size: 20px;
    color: #333;
    z-index: 100;
    text-align: center;
  }
</style>
